@import "../../scss/index.scss";

.user {


    .search-input {
        display: flex;
    }

    .lock {
        margin-top: 50px;

        margin-left: 10px;

        display: flex;

        .blocked {
            margin-left: 25px;


            background-color: deeppink;
            border: none;

            &:hover {
                background-color: darkred;
                cursor: pointer;
            }
        }

        h2 {
            margin-top: 20px;
            display: block;

        }
    }

    &__title {
        border-bottom: 1px solid $border-grey;
        padding: 15px;
    }

    h2 {
        font-size: 22px;
        margin-bottom: 0;
        font-weight: bold;
    }

    .spinner-border {
        width: 25px;
        height: 25px;
    }

    &__tweets {
        h3 {
            padding: 15px;
            font-size: 18px;
            border-bottom: 1px solid $border-grey;
        }

        >.btn.btn-primary {
            display: flex;
            margin: 0 auto;
            margin-top: 10px;
            margin-bottom: 10px;
            background-color: transparent;
            border: 0;

            &:hover,
            &:focus {
                background-color: transparent !important;
                color: $primary;
            }
        }
    }
}